    <h1 id='proj' class='skills-header'>FootNotes.JS</h1>
    <p><b>FootNotes.JS</b> - это скрипт, разработанный специально для авторов, которые хотят в своих статьях сделать что то вроде сносок, как это делается в книгах в конце страницы. По тексту здесь будут разбросаны такие "[1]" ссылки, это и есть FootNotes<span class="tooltip-link"><sup></sup></span> <span class="tooltip-hide toolTip increment"><b></b> <a href="">Привет, я FootNotes.JS!</a>
    <span class="closeTooltip">x</span></span>. Покликайте, чтобы посмотреть как он работает.</p>
    <h2>Преимущества FootNotes.JS</h2>
    <ul>
        <li>Скрипт написан полностью на чистом JavaScript<span class="tooltip-link"><sup></sup></span>
            <span class="tooltip-hide toolTip increment"><b></b> <a href="">Это ведь круто, да? Я всегда стремился к улучшению производительности.</a>
            <span class="closeTooltip">x</span></span>, без использования jQuery и прочих библиотек.</li>
        <li>Размер скрипта всего <b>4kb</b>. Вес может быть уменьшен, если в стандартных 10 сносках на страницу нет необходимости<span class="tooltip-link"><sup></sup></span>
            <span class="tooltip-hide toolTip increment"><b></b> <a href="">По дефолту скрипт поддерживает 10 сносок на 1 станице, но если нужно, это количество легко увеличить, пишите мне на мыло если не справитесь сами!</a>
                <span class="closeTooltip">x</span></span> и вы можете обойтись меньшим количеством.</li>
        <li>Простота использования<span class="tooltip-link"><sup></sup></span>
            <span class="tooltip-hide toolTip increment"><b></b> <a href="">Справился бы даже ваш кот ;)</a>
                <span class="closeTooltip">x</span></span>. Достаточно вставить HTML разметку в нужное место, и прописать текст сноски.</li>
        <li>Стиль сноски адаптивен и не будет уезжать за пределы экрана на экранах мобильных устройств.</li>
        <li>Автоматизация<span class="tooltip-link"><sup></sup></span>
            <span class="tooltip-hide toolTip increment"><b></b> <a href="">99%. Кое что всё таки сделать придется.</a>
                <span class="closeTooltip">x</span></span>. Скрипт сам проставит номера сносок, в зависимости от порядка на странице. Помимо этого он сам составит список всех сносок в конце страницы.</li>
        <li>Может быть использован для создания обычных тултипов или всплывающих окон.</li>
    </ul>
    <h2>Установка</h2>
    <ol>
        <li>Подключить CSS стиль <b><a href="../../css/proj/footNotes.css">footNotes.css</a></b> в шапке сайта.</li>
        <li>Подключить скрипт <b><a href="../../js/proj/footNotes.js">footNotes.js</a></b> как обычно, перед закрывающим тегом &lt;/body&gt;</li>
        <li>Рекомендуется, но не обязательно, также подключить библиотеку <b><a href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css">fontawesome</a></b> (для отображения стрелочки на кнопке "Сноски" в списке).</li>
        <i>Ссылки кликабельны.</i>
    </ol>
    <h2>Использование</h2>
    <p>Когда все установлено, в том месте, где необходимо установить сноску вставляем этот код:</p>
    <pre><code>&lt;span class="tooltip-link"&gt;&lt;sup&gt;&lt;/sup&gt;&lt;/span&gt;
&lt;span class="tooltip-hide toolTip increment"&gt;&lt;b&gt;&lt;/b&gt; &lt;a href=""&gt;ТЕКСТ&lt;/a&gt;
&lt;span class="closeTooltip"&gt;x&lt;/span&gt;&lt;/span&gt;</code></pre>
    <p>Дальше всё что нужно, это заменить слово "ТЕКСТ" на текст вашей сноски.</p>
    <p>Если вы хотите в конце вашей статьи выводить список всех сносок, добавьте туда этот код:</p>
    <pre><code>&lt;div align="right" style="margin-bottom: 25px;">
      &lt;span class="spoiler-name">&lt;i style="color: white;font-size: 16px;" id="spoiler-arrow" class='fa fa-chevron-down'>&lt;/i> Сноски&lt;/span>
&lt;/div>
&lt;ol id="spoiler" class="spoilerHidden">
  &lt;li id="note1">&lt;/li>
  &lt;li id="note2">&lt;/li>
  &lt;li id="note3">&lt;/li>
  &lt;li id="note4">&lt;/li>
  &lt;li id="note5">&lt;/li>
  &lt;li id="note6">&lt;/li>
  &lt;li id="note7">&lt;/li>
  &lt;li id="note8">&lt;/li>
  &lt;li id="note9">&lt;/li>
  &lt;li id="note10">&lt;/li>
&lt;/ol></code></pre>
    <p>Заполнять ничего не нужно, скрипт сам всё сделает.</p>
    <p>Пожелания, предложения можете присылать мне на <i>lestrangeqq@gmail.com</i></p>
