import { createRouter, createWebHashHistory } from 'vue-router';
import MainArea from '../components/main/MainArea';

/* Projects links */
import CssRandomizer from '../components/proj/CssRandomizer';
import FootNotes from '../components/proj/FootNotes';
import MarkText from '../components/proj/MarkText';
import NftCardComp from '../components/proj/NftCardComp';
import OrderSumComp from '../components/proj/OrderSumComp';
import ServiceCard from '../components/proj/ServiceCard';
import ProductCard from '../components/proj/ProductCard';
import SimpleClock from '../components/proj/SimpleClock';
import SimpleTodo from '../components/proj/SimpleTodo';

/* Pages */
import MyProjects from '../components/pages/MyProjects';

const routes = [
	{
		path: '/',
		name: 'MainArea',
		component: MainArea, 
		meta: {
			title: 'lestrangeQQ.github.io',
		},
	},
	//Projects Links
	{
		path: '/proj/cssRandomizer/',
		name: 'CssRandomizer',
		component: CssRandomizer,
		meta: {
			title: 'cssRandomizer',
		},
	},
	{
		path: '/proj/footNotes/',
		name: 'FootNotes',
		component: FootNotes,
		meta: {
			title: 'FootNotes.JS',
		},
	},
	{
		path: '/proj/markText/',
		name: 'MarkText',
		component: MarkText,
		meta: {
			title: 'markText.JS',
		},
	},
	{
		path: '/proj/nft-card-component/',
		name: 'NftCardComp',
		component: NftCardComp,
		meta: {
			title: 'Nft Card Component',
		},
	},
	{
		path: '/proj/order-summary-component/',
		name: 'OrderSumComp',
		component: OrderSumComp,
		meta: {
			title: 'Frontend Mentor | Order summary card',
		},
	},
	{
		path: '/proj/serviceCard/',
		name: 'ServiceCard',
		component: ServiceCard,
		meta: {
			title: 'Service Card',
		},
	},
	{
		path: '/proj/productCard/',
		name: 'ProductCard',
		component: ProductCard,
		meta: {
			title: 'Product Card',
		},
	},
	{
		path: '/proj/simpleClock/',
		name: 'SimpleClock',
		component: SimpleClock,
		meta: {
			title: 'Simple Clock',
		},
	},
	{
		path: '/proj/simpleTodo/',
		name: 'SimpleTodo',
		component: SimpleTodo,
		meta: {
			title: 'Simple ToDo List',
		},
	},
	//Pages
	{
		path: '/pages/MyProjects',
		name: 'MyProjects',
		component: MyProjects,
		meta: {
			title: 'My Projects',
		},
	},
]

const router = createRouter({
	history: createWebHashHistory(), //fix the 404 when reloading
	routes
})

router.beforeEach((to, from, next) => {
  document.title = to.meta.title //page title update
  next()
})

export default router