<template>
	<link rel="stylesheet" href="../../css/proj/simpleClock.css">
	<link href="https://fonts.googleapis.com/css?family=Orbitron&display=swap" rel="stylesheet"> 
	<div id="time"></div>
	<div id="today"></div>
	<div id="suggest"></div>

	<div id='script'></div>
</template>

<script>
	export default {
	name: 'SimpleClock',
	mounted() {	
		let appJs = document.createElement('script');
		appJs.setAttribute('src', '../../js/proj/simpleClock.js');
		document.getElementById('script').appendChild(appJs);
	},
}
</script>